import { Component, OnInit, ViewChild } from "@angular/core";
import { ThfDialogService } from "@totvs/thf-ui";
import { ActivatedRoute, Router } from "@angular/router";
import { AtendimentoService } from "./atendimento.service";
import { ConsultaEpisEntreguesComponent } from "../consulta-epis-entregues/consulta-epis-entregues.component";


@Component({
  selector: "app-atendimento",
  templateUrl: "./atendimento.component.html",
  styleUrls: ["./atendimento.component.css"],
  providers: [ConsultaEpisEntreguesComponent]
})
export class AtendimentoComponent implements OnInit {

  @ViewChild(ConsultaEpisEntreguesComponent) consulta: ConsultaEpisEntreguesComponent

  now = new Date();
  qtde_atender: string = "1,00";
  agrupar_lotes_wms: boolean = false;
  fechar_item_requisicao: boolean = false;
  data_atendimento =
    this.now.getFullYear() +
    "-" +
    (this.now.getMonth() + 1) +
    "-" +
    this.now.getDate();
  show_loading: boolean = false;

  dateNow =
    this.now.getFullYear() +
    "-" +
    (this.now.getMonth() + 1) +
    "-" +
    this.now.getDate();

  item_atender: any;

  rowActions = {
    beforeSave: this.onBeforeSave.bind(this),
    afterSave: this.onAfterSave.bind(this),
    beforeRemove: this.onBeforeRemove.bind(this),
    afterRemove: this.onAfterRemove.bind(this),
    beforeInsert: this.onBeforeInsert.bind(this),
  };

  columns = [
    { property: "cod-depos", label: "Dep", readonly: true },
    { property: "cod-localiz", label: "Localizao", readonly: true },
    { property: "it-codigo", label: "Cd. Estoque", readonly: true },
    { property: "lote", label: "Lote", readonly: true },
    // { property: 'validade', label: 'Validade', readonly: true},
    { property: "cod-refer", label: "Referncia", readonly: true },
    { property: "qtidade-dsp", label: "Saldo Disp.", readonly: true },
    { property: "qtd-atend", label: "Qtde. Atender", required: true },
    // { property: 'em_proc', label: 'Em Proc', readonly: true},
  ];

  data = [];

  constructor(
    public thfDialog: ThfDialogService,
    private router: Router,
    protected activated_route: ActivatedRoute,
    private service: AtendimentoService,
    private consultaComponente: ConsultaEpisEntreguesComponent
  ) {}

  ngOnInit() {
    this.item_atender = this.getParams();
    this.qtde_atender = this.item_atender["qtd-entreg-epi"];

    this.loadData();
  }

  onBeforeSave(row: any, old: any) {
    console.log("teste aqui", row, old);
    return row.occupation !== "Engineer";
  }

  onAfterSave(row) {
    console.log("onAfterSave(new): ", row);
  }

  onBeforeRemove(row) {
    // console.log('onBeforeRemove: ', row);

    return true;
  }

  onAfterRemove(row) {
    // console.log('onAfterRemove: ', row);
  }

  onBeforeInsert(row) {
    // console.log('onBeforeInsert: ', row);

    return true;
  }
  submit() {
    let qtd_atender_total = 0;
    for (const item of this.data) {
      if (parseFloat(item["qtd-atend"]) > item["qtidade-dsp"]) {
        this.thfDialog.alert({
          title: "Ateno",
          message:
            "A quantidade a atender no pode ser maior que o saldo disponvel.",
        });
        return this;
      }
      if (parseFloat(item["qtd-atend"]) < 0) {
        this.thfDialog.alert({
          title: "Ateno",
          message: "A quantidade a atender no pode ser negativa.",
        });
        return this;
      }

      qtd_atender_total += parseFloat(item["qtd-atend"]);
    }

    /* if(qtd_atender_total !=  parseFloat(this.qtde_atender.replace(',', '.'))){
      this.thfDialog.alert({title: 'Ateno', message: 'A quantidade total atendida deve ser igual a quantidade a atender.'});
      return this;
    } */

    if (!this.data_atendimento) {
      this.thfDialog.alert({
        title: "Ateno",
        message: "A Data de atendimento deve ser informada.",
      });
      return this;
    }

    this.setShowLoading(true);

    let saldo = [];

    for (const item of this.data) {
      saldo.push({
        "cdn-estab": item["cod-estabel"],
        "cod-depos": item["cod-depos"],
        "cod-localiz": item["cod-localiz"],
        "cod-lote": item["lote"],
        "qtd-atend": parseFloat(item["qtd-atend"]),
      });
    }

    let tt_param = {
      "cdn-funcionario": parseInt(this.item_atender["cdn-funcionario"]),
      "ep-codigo": this.item_atender["cdn-empresa"],
      "cod-estabel": this.item_atender["cod-estabel"],
      "cod-epi": this.item_atender["cod-epi"],
      "cdn-fornecedor": parseInt(this.item_atender["cdn-fornecedor"]),
      "dat-entreg-epi": this.data_atendimento,
      "tp-assinatura": parseInt(this.item_atender["tipo_autenticacao"]),
      assinatura: this.item_atender["autenticacao"],

      usuar_trans: "",
      saldo: saldo,
    };

    let data_envio = {
      metodo: "atendimentoEPI",
      paramEPI: tt_param,
    };


    //primeiro atender
    this.service.apiCrudEpi(
      //erro est aqui de no atender correto, no data_envio
      data_envio,
      (response) => {
        console.log("Aqui terceiro", response)
        if (response.items[0].hasOwnProperty("cod-erro")) {

          let string_error = "";
          for (let error of response.items) {
            string_error = string_error + error["desc-erro"] + ". ";
          }
          this.thfDialog.alert({ title: "Ateno", message: string_error });
          this.setShowLoading(false);
          return this;
        }

        this.setShowLoading(false);
        this.thfDialog.alert({
          title: "Sucesso",
          message: "Atendimento realizado com sucesso!",
        });
        this.router.navigate(["epi-entregues"]);
        /* this.consulta.goToDelete() */

      },
      (error) => {
        console.log("errooooooo", error);
        //TODO: informar erro com dialog
        this.setShowLoading(false);
        return null;
      }
    );
  }

  public getParams(property?: string) {
    let a: any = this.getActivatedRoute();
    let b: any = a.queryParams;
    let v = b.getValue();

    if (!property) {
      return v;
    }
    if (!v.hasOwnProperty(property)) {
      return null;
    }
    return v[property];
  }

  public getActivatedRoute() {
    return this.activated_route;
  }

  getShowLoading() {
    return this.show_loading;
  }
  setShowLoading(v) {
    this.show_loading = v;
    return this;
  }

  loadData() {
    this.setShowLoading(true);
    let tt_param = {
      "cdn-funcionario": parseInt(this.item_atender["cdn-funcionario"]),
      "ep-codigo": this.item_atender["cdn-empresa"],
      "cod-estabel": this.item_atender["cod-estabel"],
      "cod-epi": this.item_atender["cod-epi"],
      "cdn-fornecedor": parseInt(this.item_atender["cdn-fornecedor"]),
      "dat-entreg-epi": this.data_atendimento,
    };

    let data_envio = {
      metodo: "saldoEPI", //TODO: Rever o mtodo que tem que chamar
      paramEPI: tt_param,
    };

    this.service.apiCrudEpi(
      data_envio,
      (response) => {
        console.log("Aqui primeiro", data_envio);
        console.log("Aqui segundo",response);
        //TODO: verificar como ser o retorno para tratar caso de erro
        if (response.total == 0) {
          this.thfDialog.alert({
            title: "Ateno",
            message: "No existe saldo para atendimento!",
          });
          this.setShowLoading(false);
          return this;
        }

        if (response.items[0].hasOwnProperty("cod-erro")) {
          let string_error = "";
          for (let error of response.items) {
            string_error = string_error + error["desc-erro"] + ". ";
          }
          this.thfDialog.alert({ title: "Ateno", message: string_error });
          this.setShowLoading(false);
          return this;
        }

        this.data = response.items;
        this.setShowLoading(false);
      },
      (error) => {
        console.log("errooooooo", error);
        //TODO: informar erro com dialog

        this.setShowLoading(false);
        return null;
      }
    );
  }
}
